
import styles from './Tower.module.css';
import React from 'react'

const Timer = () => {
    return (
        <div className={styles.showTime} >
            <p style={{padding: "0px", margin: "0px", fontSize: "2.4vmax"}}><b>TIME</b> <br/></p>
            <p style={{ paddingLeft: "4px", margin: "0px", height: "1vmax"}}>
                <b id="min" style={{fontSize: "2.4vmax", top: "1vh"}}>  0 </b>   :   <b id="sec"
                style={{fontSize: "2.4vmax", top: "1vh"}}>0</b>
            </p>
        </div>
    )
}

export default Timer
